.rating {
  width: 100vw;
  height: 75vh;
  margin-top: 3rem;
}

.ratinga {
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 4rem;
}

.ratinga h1 {
  color: var(--pri);
  background-color: white;
  box-shadow: 10px 10px 30px rgb(177, 177, 177);
  width: 21vw;
  height: auto;
  font-size: 4rem;
  font-weight: 400;
  border: 1px solid white;
  border-radius: 2.5rem;
  padding: 1.5rem 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--ff);
}

.ratings {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  flex-wrap: wrap;
}

img {
  height: 14vh;
  width: 6.8vw;
}

.rating-item {
  text-align: center;
  width: 25vw;
  height: 45vh;
  padding: 1rem;
}

.rating-item h3 {
  padding: 1rem;
  color: rgb(0, 0, 0);
  font-size: 3rem;
  font-family: var(--ff);
}

.rating-item p {
  font-size: 2.5rem;
  font-weight: 400;
  color: #000000;
  font-family: var(--ff);
}

@media only screen and (max-width: 600px) {
  .rating {
    height: 110vh;
    margin-top: 6rem;
  }

  .ratings {
    gap: 1rem;
  }

  .ratinga {
    padding: 0rem;
  }

  .ratinga h1 {
    width: 50vw;
    height: auto;
    font-size: 3rem;
  }

  img {
    height: 9vh;
    width: 18vw;
  }

  .rating-item {
    width: 50vw;
    height: auto;
    margin-top: 4rem;
    padding: 0rem;
  }

  .rating-item h3 {
    padding: 0rem;
    font-size: 3.4rem;
  }

  .rating-item p {
    font-size: 2rem;
    font-weight: 400;
  }
}
