.slogan-with-image {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
  height: 70vh;
  background-color: var(--pri);
}

.para {
  height: 45vh;
  width: 32vw;
}

.para h1 {
  color: white;
  font-size: 5rem;
  font-weight: 400;
  font-family: var(--ff);
  padding-bottom: 1.5rem;
}

.para li {
  font-size: 2rem;
  font-weight: 350;
  color: white;
  list-style-type: none;
  font-family: var(--ff);
  padding-bottom: 3rem;
}

.para a {
  padding: 0.6rem 4rem;
  font-size: 2rem;
  font-weight: 600;
  text-decoration: none;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  border-radius: 2rem;
}

.para a:hover {
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.image {
  margin-top: 16.2rem;
}

.img {
  height: 47vh;
  width: 20vw;
  border-radius: 18rem 18rem 0rem 0rem;
  background-color: white;
}

@media only screen and (max-width: 600px) {
  .slogan-with-image {
    display: grid;
    width: 100vw;
    height: 60.3vh;
    gap: 0rem;
  }

  .para {
    height: 30vh;
    width: 80vw;
    padding: 0rem;
    margin: 2rem;
  }

  .para h1 {
    font-size: 3rem;
  }

  .para li {
    padding-top: 0rem;
    font-size: 1.5rem;
    font-weight: 400;
  }

  .para a {
    padding: 0.5rem 1.4rem;
    font-size: 1.7rem;
    font-weight: 600;
    text-decoration: none;
    border-radius: 2rem;
  }

  .image {
    display: flex;
    justify-content: center;
    margin-top: 0rem;
    margin-right: 0rem;
  }

  .img {
    height: 25vh;
    width: 50vw;
    border-radius: 25rem 25rem 0rem 0rem;
    background-color: white;
  }
}
