.popupStyle{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgba(10, 98, 186, 0.5);
    z-index: 1000;
    width: 100% ;
     display: flex;
     justify-content: center;
     align-items: center;
     padding: 0rem;
}

.contact-form {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family:var(--ff);
    background-color:var(--pri);
    padding: 5rem 5rem;
    margin: 6rem;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    height: auto;
    max-width: 50rem;
    color: white;
}

.contact-form h2 {
    margin-bottom: 2rem;
    text-align: center;
}

.contact-form label {
    display: block;
    margin-bottom: .5rem;
}

.contact-form input, .contact-form textarea {
    width: 84%;
    padding: 1.2rem;
    margin-bottom: 1.5rem;
    border:none;
    border-radius: .4rem;
    background-color: #ffffff;
    color: #000000;
}

.contact-form button[type="submit"] {
    font-family:var(--ff);
    font-size: 2rem;
    background-color: #ffffff;
    color: black;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 40%;
    padding: 1rem;
    margin-bottom: .5rem;
    border: none;
    border-radius: 1rem;
}

.contact-form button[type="submit"]:hover {
    background-color: #000000;
    color: white;
}

.contact-form button[type="button"] {
    font-family:var(--ff);
    font-size: 2rem;
    background-color: #ffffff;
    color: black;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 40%;
    padding: 1rem;
    margin-bottom: .5rem;
    border: none;
    border-radius: 1rem;
}

.contact-form button[type="button"]:hover {
    background-color: #000000;
    color: white;
}

@media only screen and (max-width: 600px) {

    .contact-form {
        padding: 1rem 1rem;
        margin: 2rem;
    }

    .popupStyle{
         padding: 2rem;
         height:100% ;
    }
    
    .contact-form button[type="submit"] {
        font-size: 1.6rem;
        width: 36%;
        padding: .7rem;
    }

    .contact-form button[type="button"] {
        font-size: 1.6rem;
        width: 36%;
        padding: .7rem;
    }

}




