.homepage {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: start;
}

.homepage .bg-clip {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 68vh;
  z-index: -1;
}

.video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  opacity: 0.5;
}

.homepage .left-side {
  margin-left: 16rem;
  margin-top: 2rem;
}

.left-side {
  width: 40vw;
  height: 40rem;
  display: grid;
  align-items: center;
  justify-content: center;
}

.left-side h2 {
  text-align: center;
  width: 48rem;
  height: 0rem;
  font-size: 4rem;
  color: rgb(0, 0, 0);
  margin-bottom: 2rem;
  font-family: var(--ff);
}

.left-side h2 span {
  color: var(--pri);
}

.lsc,
.lsa {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Hcarousel {
  width: 35rem;
  height: auto;
  padding: 1rem 0rem;
  font-size: 2.3rem;
  border-radius: 6rem;
  background-color: var(--pri);
  font-family: var(--ff);
}

.Hcarousel h1 {
  color: rgb(255, 255, 255);
}

.lsa {
  margin-top: -10rem;
}

.lsa a {
  padding: 1rem 2rem;
  font-size: 2rem;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  text-decoration: none;
  border-radius: 5rem;
  transition: 0.1s;
  font-family: var(--ff);
}

a:hover {
  background-color: rgb(255, 255, 255);
  color: black;
  font-weight: 800;
  border: 2px solid black;
}

@media only screen and (max-width: 600px) {
  .homepage {
    height: 40vh;
  }

  .homepage .bg-clip {
    width: 100vw;
    height: 2vh;
  }

  .video {
    object-fit: cover;
    width: 100vw;
    height: 40vh;
  }

  .homepage .left-side {
    margin-left: 8rem;
    margin-top: 3rem;
  }

  .left-side {
    width: 60vw;
    height: 30vh;
  }

  .left-side h2 {
    width: 60vw;
    height: 2rem;
    font-size: 2.5rem;
    margin-bottom: 0rem;
  }

  .Hcarousel {
    margin-top: 2rem;
    width: 60vw;
    height: auto;
    padding: 1rem;
    font-size: 2rem;
    border-radius: 4rem;
  }

  .lsa a {
    padding: 0.7rem 1rem;
    font-size: 1.8rem;
    border-radius: 2rem;
    margin-top: 8rem;
  }
}
