.contact-us {
  width: 100vw;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  border-top: 1px solid black;
}

.contact-column {
  width: 32vw;
  height: 60vh;
  background-color: #ffffff;
  padding: 2rem;
}

.contact-column h2 {
  font-size: 3rem;
  color: rgb(0, 0, 0);
  font-family: var(--ff);
}

.contact-column h1 {
  margin-top: 3rem;
  font-weight: 200;
  font-size: 1.2rem;
  font-family: var(--ff);
  color: #000000;
}

.contact-column p,
.contact-column form,
.contact-column ul {
  font-size: 2.3rem;
  margin-top: 2rem;
  font-family: var(--ff);
}

.contact-column p {
  color: rgb(0, 0, 0);
  font-size: 2rem;
}

.contact-column p a {
  color: var(--pri);
  text-decoration: none;
}

.contact-column form label {
  margin-bottom: 1rem;
  color: #000000;
}

.contact-column form input {
  width: 60%;
  height: 7vh;
  padding: 0.6rem;
  font-size: 1.7rem;
  border-radius: 3px;
}

.contact-column form a {
  font-size: 2.3rem;
  padding: 1rem 2rem;
  margin-left: 1rem;
  text-decoration: none;
  background-color: var(--pri);
  color: #ffffff;
  border: 0px solid rgb(0, 0, 0);
  border-radius: 3rem;
  cursor: pointer;
  font-family: var(--ff);
}

.contact-column form a:hover {
  background-color: #000000;
  font-weight: 400;
}

.contact-column ul {
  list-style-type: none;
}

.contact-column ul li {
  margin-bottom: 1rem;
}

.contact-column ul li a {
  color: #000000;
  font-size: 2.2rem;
  font-weight: 500;
  text-decoration: none;
  font-family: var(--ff);
}

.contact-column ul li a:hover {
  text-decoration: none;
  color: var(--pri);
  border: none;
}

.contact-column p a:hover {
  text-decoration: underline;
  border: none;
  background-color: #ffffff;
  color: rgb(0, 0, 0);
}

@media only screen and (max-width: 600px) {
  .contact-us {
    display: grid;
    width: 100vw;
    height: 78vh;
  }

  .contact-column {
    width: 100vw;
    height: auto;
  }

  .contact-column h2 {
    font-size: 2rem;
  }

  .contact-column p,
  .contact-column form,
  .contact-column ul {
    font-size: 2rem;
    margin-top: 0rem;
  }

  .contact-column p {
    font-size: 2rem;
  }

  .contact-column ul li {
    margin-bottom: 0rem;
  }

  .contact-column ul li a {
    font-size: 1.7rem;
    font-weight: 400;
  }
}
