.nav {
  height: 15vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-link {
  display: flex;
  align-items: center;
  gap: 4rem;
  padding-right: 7rem;
}

.nav h1 {
  padding: 1.5rem;
  margin-left: 3rem;
  border: 1px solid #ffffff;
  border-radius: 3rem;
  box-shadow: 5px 5px 6px rgba(111, 111, 111, 0.5);
  font-size: 4rem;
  font-weight: 500;
  color: var(--pri);
}

.nav a {
  color: var(--pri);
  font-size: 2.5rem;
  font-weight: 500;
  text-decoration: none;
  transition: 0.3s;
  font-family: var(--ff);
}

.nav i {
  display: none;
}

.nav a:hover {
  font-size: 2.8rem;
  color: #000000;
  border: none;
}

@media only screen and (max-width: 600px) {
  .nav {
    width: 100vw;
    height: 12vh;
  }

  .nav h1 {
    padding: 1.2rem;
    margin-left: 2rem;
    border-radius: 2rem;
    font-size: 2rem;
  }

  .nav a {
    display: none;
  }

  .nav i {
    font-size: 11vw;
    display: block;
    font-weight: 900;
    color: var(--pri);
    margin-top: 0.8rem;
    margin-left: 0rem;
  }

  .menu {
    z-index: 2;
    height: 35vh;
    width: 40vw;
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color: var(--pri);
    border: 1px solid white;
    margin-top: 1.9rem;
    right: 0rem;
    padding: 2rem 0rem 0rem 2rem;
  }
  .menu li a {
    display: block;
    color: rgb(255, 255, 255);
    font-family: var(--ff);
  }

  .menu li {
    list-style-type: none;
    padding: 1rem;
  }
}


.nav-bar li button {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
}

.nav-bar li a:hover,
.nav-bar li button:hover {
  background-color: #111;
}

.contact-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-form {
  width: 60rem;
  height: auto;
  background-color: white;
  padding: 2rem;
  border-radius: 5px;
  font-size: 2.2rem;
  font-family: var(--ff);
  background-color: var(--pri);
}

.contact-form h2 {
  margin-top: 0;
}

.contact-form form {
  display: flex;
  flex-direction: column;
}

.contact-form label {
  margin-bottom: 10px;
}

.contact-form button {
  margin-top: 10px;
}